export default {
  name: 'AllPersonalities',
  data() {
    return {
      window: {
        width: 0,
      },
      colors: {
        selectedObject: {
          color: '#eee',
        },
        classColor: '',
        // Architect, Commander, Debater, Logician
        analysts: '#E8D8E0',
        // Advocate, Campaigner, Mediator, Protagonist
        diplomats: '#DBEEE6',
        // Consul, Defender, Executive, Logistician
        sentinels: '#CDE6E7',
        // Adventurer, Entertainer, Entrepreneur, Virtuoso
        explorers: '#F7E7CB',
      },
      barScale: 15,
      counts: {
        members: 0,
        architects: 0,
        commanders: 2,
        debaters: 2,
        logicians: 4,
        advocates: 8,
        campaigners: 2,
        mediators: 4,
        protagonists: 4,
        consuls: 1,
        defenders: 3,
        executives: 0,
        logisticians: 2,
        adventurers: 2,
        entertainer: 0,
        entrepreneur: 0,
        virtuoso: 0,
      },
      show: {
        architects: false,
        commanders: false,
        debaters: false,
        logicians: false,
        advocates: false,
        campaigners: false,
        mediators: false,
        protagonists: false,
        consuls: false,
        defenders: false,
        executives: false,
        logisticians: false,
        adventurers: false,
        entertainer: false,
        entrepreneur: false,
        virtuoso: false,
     }
    }
  },
  mounted() {
    this.window.width = this.$refs.guildWindow.clientWidth;
  },
  created() {
    window.addEventListener('resize', this.handleResize),
    this.handleResize();
  },
  destroyed() {
    window.removeEventListener('resize', this.handleResize)
  },
  methods: {
    handleResize() {
      this.window.width = this.$refs.guildWindow.clientWidth;
    }
  },
  computed: {
    erectBar() {
      return {
        backgroundColor: this.colors.classColor,
        width: this.window.width/this.barScale * this.counts.members + 'px',
      }
    },
    erectIcon() {
      return {
        marginLeft: this.window.width/this.barScale * this.counts.members + 'px',
      }
    },
  }
}
